.App {
  padding: 20px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  max-width: 1600px;
  margin: 0 auto;
}

.symbols {
  grid-column: span 9;
  margin-right: 10px;
}

.symbols-block {
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

.birthday-block {
  grid-column: span 3;
}

.symbol-item {
  grid-column: span 1;
}

.employees-birthday {
  border: 1px solid gray;
  height: 100%;
  padding: 10px;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 1000px) {
 
  .birthday-block {
    grid-column: span 4;
  }
  .symbols {
    grid-column: span 8;
  }
  .symbol-item {
    grid-column: span 2;
  }
}

@media only screen and (max-width: 700px) {
  h1 {
    font-size: 20px;
  }
  .employees-birthday h1 {
    margin-top: 0;
  }
  .birthday-block {
    grid-column: span 6;
  }
  .symbols {
    grid-column: span 6;
  }
  .symbol-item {
    grid-column: span 4;
  }
}